import React, { useState } from 'react'
import Button from './Button'
import sanat from '../resources/k-sanalista.js'

const Main = ({ setBody, voteState }) => {

  const randomKWord = () => {
    const rand = Math.floor(Math.random() * (sanat.length - 1))
    const randK = sanat[rand]
    return randK
  }

  const [dailyK, setDailyK] = useState(randomKWord())

  return (
    <div className='Container'>
      <h3 className='Heading'>Aihepoolivalintasovellus</h3>
      {voteState.voteOpen === false && <p>Äänestys on suljettu.</p>}
      {voteState.voteOpen === true && <Button type='Button-aanestys' text='Äänestä' handleClick={() => setBody('topicSelection')} />}
      <div className='Paivan-tk'>
        <h3>Päivän .tk:</h3>
        <p>Tiedon {dailyK}</p>
        <Button type='Uusi-button' text='Uusi' handleClick={() => setDailyK(randomKWord())} />
      </div>
    </div>
  )
}

export default Main
